				this.isLogin = uni.getStorageSync('isLogin') || false
				const oldUserInfo = uni.getStorageSync('userInfo') || {}
				this.userInfo = {
					name: oldUserInfo.name || 'ÂøÉÂ±øÁî®Êà∑',
					email: oldUserInfo.email || '',
					avatar: oldUserInfo.avatar || '',
					bio: oldUserInfo.bio || '',
